import React from "react"
import TodoItem from "./TodoItem.js"
import todosData from "./todosData.js"
import PopForm from "./PopUpForm.js"


class App extends React.Component {
    constructor() {
        super()
        this.state = {
            todos: todosData,
            task: '',
            hideOrNot: "todo-list",
            pressed: true,
            addNewOrBack: "Add New",
            PopUpForm: "hide",
            tempDate: '',
            tempPrio: 'High',
            tempQuantity: ''
        }
        this.AddingElem = this.AddingElem.bind(this)
        this.HandleText = this.HandleText.bind(this)
        this.handleChange = this.handleChange.bind(this)
        this.Deletion = this.Deletion.bind(this)
        this.AddNew = this.AddNew.bind(this)
    }

    handleChange(id) {
        this.setState(prevState => {
            const updatedTodos = prevState.todos.map(todo => {
                if (todo.id === id) {
                    todo.completed = !todo.completed
                }
                return todo
            })
            return {
                todos: updatedTodos
            }
        })
    }

    //Deleting elements from current state
    Deletion(id) {
        let index = this.state.todos.findIndex(p => p.id === id);
        if (index !== null) {
            this.state.todos.splice(index, 1)
            this.setState({ todos: this.state.todos })
        }
    }

    //Adding new elements into current state
    HandleText(event) {
        console.log("happening")
        console.log("event val: ", event.target.value)
        let nam = event.target.name
        let val = event.target.value
        if (nam === "taskName" && val !== "" && val != null) {
            this.setState({ task: val })
            console.log(this.state.task)
        } else if (nam === "priority" && val !== "" && val != null) {
            this.setState({ tempPrio: val })
            console.log(this.state.tempPrio)
        } else if (nam === "quantity" && val !== "" && val != null) {
            this.setState({ tempQuantity: val + " hours" })
            console.log(this.state.tempQuantity)
        } else if (nam === "dueDate" && val !== "" && val != null) {
            this.setState({ tempDate: val })
            console.log(this.state.tempDate)
        } else { console.log("Something is wrong") }

    }

    //Adding new elements into current state
    AddingElem() {
        if (this.state.task !== "" && this.state.tempPrio !== "" && this.state.tempDate !== "" && this.state.tempQuantity !== "") {
            console.log("yes")
            let todo = {
                id: this.state.todos.length + 1,
                text: this.state.task,
                date: this.state.tempDate,
                timeRequired: this.state.tempQuantity,
                priority: this.state.tempPrio,
                completed: false
            }
            this.state.todos.push(todo)
            this.setState({ todos: this.state.todos })
            alert("New Submission added")
            console.log(this.state.todos)
            this.setState({ hideOrNot: "todo-list", PopUpForm: "hide" })
            console.log(this.state.hideOrNot)
        }
        else if (this.state.task === "") {
            alert("Please enter a valid value")
        }
    }

    /*When AddNew button is clicked, the current todolist will be hidden*/
    AddNew() {
        let pressing = this.state.pressed
        this.setState({ pressed: !pressing })
        if (this.state.pressed) {
            this.setState({ hideOrNot: "todo-list hide", PopUpForm: "", addNewOrBack: "Back" })
        } else (this.setState({ hideOrNot: "todo-list", PopUpForm: "hide", addNewOrBack: "Add New" }))

    }

    /* filter todolist based on date*/
    FilterDate(event){
        let todayDate = new Date()
        console.log("date: ",todayDate.getDate())
        // if(event.target.value === "Today"){
        //     let duplicateCurrentTodos = this.setState()
            
        // }
        // else if (event.value === "This Week"){

        // }
        // else if(event.value === "This Month"){

        // }
        // else if(event.value === "All"){

        // }   

    }


    render() {
        const todoItems = this.state.todos.map(item => <TodoItem key={item.id} item={item} handleChange={this.handleChange} Deletion={this.Deletion} />)
        return (
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        <div className="btn-group btn-group-lg">
                        <button type="button" className="btn btn-primary" onClick={this.AddNew}> {this.state.addNewOrBack}</button>
                        <button type="button" className="btn btn-primary" value ="All" onClick={this.FilterDate}>All</button>
                        <button type="button" className="btn btn-primary" value = "Today" onClick={this.FilterDate}>Today</button>
                        <button type="button" className="btn btn-primary" value = "This Week" onClick={this.FilterDate}>This Week</button>
                        <button type="button" className="btn btn-primary" value = "This Month" onClick={this.FilterDate}>This Month</button>
                        </div>
                        

                        <div className={this.state.hideOrNot}>
                            <table align="center" className="table  table-responsive">
                                <thead className="thead-dark">
                                    <tr border="none">
                                        <th></th>
                                        <th>Task</th>
                                        <th>Estimate of time required</th>
                                        <th>Due Date</th>
                                        <th>Priority</th>
                                        <th>Actions</th>

                                    </tr>
                                </thead>
                                <tbody>
                                    {todoItems}
                                </tbody>
                            </table>
                        </div>
                        <div className={this.state.PopUpForm}>
                            <PopForm handleChange={this.handleChange} AddingElem={this.AddingElem} handleTEXT={this.HandleText} tempPrio={this.state.tempPrio} />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default App